/**
 * Workspace fetcher - queries user's accessible workspaces from Supabase
 */

import { getValidSession } from "../auth/session-manager.js";
import { logger } from "../utils/logger.js";
import { getSupabaseClient } from "./client.js";

export interface Workspace {
  id: string;
  name: string;
  created_at: string;
}

/**
 * Fetch all workspaces the current user has access to
 * Queries workspace_memberships joined with workspaces table
 */
export async function fetchUserWorkspaces(): Promise<Workspace[]> {
  try {
    // Get valid session (will refresh if needed)
    const session = await getValidSession();
    if (!session) {
      throw new Error("Not authenticated");
    }

    // Create Supabase client (automatically uses session token)
    const supabase = await getSupabaseClient();
    if (!supabase) {
      throw new Error("Failed to create Supabase client");
    }

    logger.debug("Fetching workspaces for user", { userId: session.userId });

    // Query workspace_memberships joined with workspaces
    const { data, error } = (await supabase
      .from("workspace_memberships")
      .select(
        `
        workspace:workspaces (
          id,
          name,
          created_at
        )
      `,
      )
      .eq("user_id", session.userId)) as {
      data: Array<{
        workspace: {
          id: string;
          name: string;
          created_at: string;
        } | null;
      }> | null;
      error: unknown;
    };

    if (error) {
      logger.error("Failed to fetch workspaces", error);
      const errorMessage = error instanceof Error ? error.message : String(error);
      throw new Error(`Failed to fetch workspaces: ${errorMessage}`);
    }

    if (!data || data.length === 0) {
      logger.info("No workspaces found for user");
      return [];
    }

    // Extract workspace objects and flatten
    const workspaces: Workspace[] = [];
    for (const item of data) {
      if (item.workspace && !Array.isArray(item.workspace)) {
        workspaces.push({
          id: item.workspace.id,
          name: item.workspace.name,
          created_at: item.workspace.created_at,
        });
      }
    }

    logger.info("Fetched workspaces", { count: workspaces.length });

    return workspaces;
  } catch (error) {
    logger.error("Error fetching workspaces", error);
    throw error;
  }
}

/**
 * Fetch a specific workspace by ID
 */
export async function fetchWorkspaceById(workspaceId: string): Promise<Workspace | null> {
  try {
    const workspaces = await fetchUserWorkspaces();
    return workspaces.find((ws) => ws.id === workspaceId) || null;
  } catch (error) {
    logger.error("Error fetching workspace by ID", error);
    throw error;
  }
}
