/**
 * Supabase client initialization
 *
 * Creates authenticated Supabase client using session tokens
 */

import { createClient, type SupabaseClient } from "@supabase/supabase-js";
import type { Database } from "@zest/types/database.types";
import { getValidSession } from "../auth/session-manager.js";
import { SUPABASE_ANON_KEY, SUPABASE_URL } from "../config/constants.js";
import { logger } from "../utils/logger.js";

/**
 * Get authenticated Supabase client
 * Returns null if not authenticated or if configuration is missing
 */
export async function getSupabaseClient(): Promise<SupabaseClient<Database> | null> {
  try {
    // Check configuration
    if (!SUPABASE_URL || !SUPABASE_ANON_KEY) {
      logger.warn("Supabase configuration missing (URL or anon key)");
      return null;
    }

    // Get valid session (auto-refreshes if needed)
    const session = await getValidSession();
    if (!session) {
      logger.debug("No valid session available, skipping Supabase client creation");
      return null;
    }

    // Create authenticated client
    const client = createClient<Database>(SUPABASE_URL, SUPABASE_ANON_KEY, {
      auth: {
        persistSession: false, // We manage session ourselves
      },
      global: {
        headers: {
          Authorization: `Bearer ${session.accessToken}`,
        },
      },
    });

    logger.debug("Supabase client created successfully");
    return client;
  } catch (error) {
    logger.error("Failed to create Supabase client", error);
    return null;
  }
}

/**
 * Check if Supabase is configured and accessible
 */
export function isSupabaseConfigured(): boolean {
  return Boolean(SUPABASE_URL && SUPABASE_ANON_KEY);
}
