/**
 * Profile metadata updater
 *
 * Updates user profile metadata to track Claude Code plugin installation
 * Similar to Chrome extension's updateChromeExtensionMetadata()
 */

import { logger } from "../utils/logger.js";
import { getSupabaseClient } from "./client.js";

/**
 * Updates the user's profile metadata to track Claude Code plugin installation
 * Merges with existing extension data to preserve other extension info (chrome, vscode, cursor, etc.)
 *
 * @param userId - User ID from authentication session
 * @param version - Plugin version (e.g., "0.0.41")
 */
export async function updateClaudeCodeMetadata(userId: string, version: string): Promise<void> {
  try {
    logger.info("Updating Claude Code plugin metadata", { userId, version });

    // Get authenticated Supabase client
    const supabase = await getSupabaseClient();
    if (!supabase) {
      throw new Error("Supabase client not available");
    }

    // Fetch existing profile metadata
    const { data: existingProfile, error: fetchError } = await supabase
      .from("profiles")
      .select("metadata")
      .eq("id", userId)
      .single();

    if (fetchError) {
      logger.warn("Failed to fetch existing profile metadata:", fetchError);
    }

    // Merge with existing metadata
    const existingMetadata = (existingProfile?.metadata as Record<string, any>) || {};
    const existingExtensions = existingMetadata?.extensions || {};

    const updatedMetadata = {
      ...existingMetadata,
      extensions: {
        ...existingExtensions,
        claudeCode: {
          installed: true,
          version: version,
          lastSeen: new Date().toISOString(),
        },
      },
    };

    // Upsert profile with updated metadata
    const { error: upsertError } = await supabase.from("profiles").upsert({
      id: userId,
      metadata: updatedMetadata,
    });

    if (upsertError) {
      throw new Error(`Failed to update profile metadata: ${upsertError.message}`);
    }

    logger.info("Claude Code plugin metadata updated successfully");
  } catch (error) {
    logger.error("Failed to update Claude Code metadata", error);
    throw error;
  }
}
