/**
 * Plugin version utilities
 *
 * Reads version from plugin.json manifest
 */

import { readFileSync } from "node:fs";
import { dirname, join } from "node:path";
import { fileURLToPath } from "node:url";
import { logger } from "./logger.js";

/**
 * Get the current plugin version from plugin.json
 * @returns Plugin version string (e.g., "0.0.41") or "unknown" if unable to read
 */
export function getPluginVersion(): string {
  try {
    const __dirname = dirname(fileURLToPath(import.meta.url));
    const pluginJsonPath = join(__dirname, "../../.claude-plugin/plugin.json");
    const pluginJson = JSON.parse(readFileSync(pluginJsonPath, "utf-8"));
    return pluginJson.version || "unknown";
  } catch (error) {
    logger.warn("Failed to read plugin version from plugin.json", error);
    return "unknown";
  }
}
